@import url(https://fonts.googleapis.com/css?family=Bungee&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body{cursor:pointer;font-family:'Bungee', 'Helvetica';color:#626766}.small-screen-text{font-size:30px;font-size:26px;font-weight:bold;color:#0000007a;font-family:Arial, Helvetica, sans-serif;box-shadow:0 4px 8px 0 rgba(0,0,0,0.2);background-color:#f4f0ff;margin:50px}@media (max-width: 1200px){.game{display:none}}@media (min-width: 1200px){.small-screen-text{display:none}.game{display:block}}

.Hangmanword{color:red}.Hangmanword ul{list-style-type:none;display:flex;justify-content:center;flex-wrap:wrap;padding:0}.Hangmanword ul li{font-size:45px;padding:5px;margin:0}.Hangmanword ul li.space{margin:0 10px}

.keyboard ul{list-style-type:none;display:flex;justify-content:center;flex-wrap:wrap}.keyboard ul li{font-size:35px;width:45px;margin:10px 10px;text-align:center;border:2px solid black}.keyboard ul li.clicked{color:#d4d3d3;border:2px solid #d4d3d3}

.Playagain{padding:10px 0px;background-color:#fff707;margin:40px auto;border-radius:5px;width:250px;text-align:center;font-weight:600;-webkit-transition:all 0.2s ease;transition:all 0.2s ease}.Playagain.active{width:350px}.Playagain:active{-webkit-transform:scale(1.1, 1.1);transform:scale(1.1, 1.1);background-color:#faab33}

.Hangman h1{font-size:70px}.Hangman .Hangman-image{width:300px;margin:0 auto}

